<template>
  <div id="app">
    <v-app id="inspire">
      <router-view/>
    </v-app>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  created()
  {
    this.setTitle();
    this.getProducts();
  },
  mounted()
  {
    var obj = this;
    setTimeout(function(){
      console.log(obj.$store.getters.getProducts);
    },1000);
  },
  methods:{
    setTitle()
    {
      document.title=this.website.title;
    },
    setProductsValue(productos)
    {
      this.$store.commit('changeProductsState',productos);
    },
    getProducts()
    { 
      var Obj = this;
      axios.get(`${this.website.api.url}productos`,{
        headers:{
          Accept : 'application/json',
          // 'Authorization' => 'Bearer '.$accessToken,
          }
      }).then((response)=>{
          Obj.setProductsValue(response.data);
      });
    }
  },
  computed:{
    website()
    {
      return this.$store.getters.getWebsiteInfo;
    }
  },
  watch:{
    '$route'(to,from)
    {
      this.setTitle();
    }
  }
}
</script>

<style>
</style>



