<template>
	<div>
		<navbar :items="navbar.items"/>
	    <v-content :style="{'padding-bottom':'100px','padding-left':'0px','padding-right':'0px','margin-left':'0px','margin-right':'0px'}">
	      <router-view/>
	      <br>
	      
	    </v-content>
		<my-footer />
	</div>
</template>
<script>
	import navbarComp from '../../components/Navbar.vue';
	import footerComp from '../../components/Footer.vue';
	export default{
		components:{navbar:navbarComp, 'my-footer': footerComp},
		mounted()
		{
		},
		data:()=>({
			navbar:{
				items:[
					{
						icon:'home',
						text:'Inicio',
						url:'/'
					},
					{
						icon:'apps',
						text:'Productos',
						url:'/productos'
					}
				]
			}
		})
	}
</script>
<style lang="scss">
	
</style>


