<template>
	<div class="home">
		<br>
		<br>
 		<v-layout align-center row wrap class="layout">
    		<v-flex text-xs-center xs12 sm12 md12 lg12>
		      <v-card class="card">
		       <center>
		       		<br>
		        	<v-img :src="boxImage" :style="{'width':'200px','height':'200px','padding':'20px'}" aspect-ratio="1"></v-img>
				</center>

		        <v-card-title primary-title :style="{'width':'100%'}">
		          <div :style="{'width':'100%'}">
		          	<center>
			            <h3 class="headline mb-0">Excelente servicio</h3>
			            <div>
							Cuidamos tanto tu mercaderia tanto como tu bolsillo.
							La mercadería que se encuentra depositada en nuestras instalaciones es tratada con el mayor de los cuidados para que el producto llegue sano y salvo a destino, también siempre intentamos mantener un stock amplio y precios accesibles tanto para comercios de grandes proporciones al igual que a pequeños kioscos y/o almacenes.
							Mantenemos una excelente relación precio y calidad por lo tanto somos una de las empresas con más trayectoria y experiencia de la zona.
			          		<br>

			            </div>
			        </center>
		          </div>
		        </v-card-title>
		      </v-card>
		    </v-flex>
		</v-layout>
	</div>
</template>
<script>
	import boxImageSVG from '../../assets/box.svg';
	export default{
		mounted()
		{
			this.$store.commit('SearcherActiveState',false);
		},
		data:()=>({
			boxImage: boxImageSVG
		}),
		methods:{
			
		},
		computed:{
		}
	}
</script>
<style lang="scss">
	.home{
		.layout
		{
			width: 100% !important;
			.card
			{
				min-height: calc(100vh - 200px) !important;
			}
		}
	}
</style>


