import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);

export const store = new Vuex.Store({
	state:{
		website:{
			title:'Distribuidora Biarritz',
			searcherActive:false,
			colors:{
				primary: '#e74c3c',
				secondary: '#424242',
				accent: '#FF8A80',
				error: '#FF5252',
				info: '#2196F3',
				success: '#4CAF50',
				warning: '#FFC107'
			},
               api:{
                    url:'https://xioncorporation.net/distribuidora-biarritz-catalogo/'
               }
			
		},
		productsSearcher:'',
		products: null,
	},
	getters:{
		getWebsiteInfo(state)
		{
			return state.website;
		},
		getProducts(state)
		{
			if(state.products != null)
               {
                    return state.products.filter((product) => product.name.toLowerCase().includes(state.productsSearcher.toLowerCase()));
               }
		}
	},
	mutations:{
		changeSearcherState(state,value)
		{
			state.productsSearcher = value;
		},
		SearcherActiveState(state, value)
		{
			state.website.searcherActive = value;
		},
          changeProductsState(state,value)
          {
               state.products = value;
          }
	},
	actions:{

	}
});


