<template>
	<div>
		<v-alert
			:value="true"
			type="info"
			v-show="withoutProductsAlert"
		>
			No se encontraron productos.
		</v-alert>
		<br>
		 <v-slider
		  :style="{'width':'96%','margin-left':'3%'}"
          v-model="wantSeePorcent"
          label="Cantidad de productos: (%)"
          thumb-label="always"
          thumb-color="blue"
          class="hidden-sm-and-down"
          @change="wantSeeChanged()"
       	 ></v-slider>
		<v-hover class="hidden-sm-and-down" v-for="(product,index) in productos"
            :key="index" v-if="index <= wantSee">
		    <v-card
		      @click="goToProduct(product.id)"
		      slot-scope="{ hover }"
		      color="grey lighten-4"
		      :style="{'display': 'inline-block','width':'30% !important', 'margin-left':'3%','cursor':'pointer', 'margin-top':'30px'}"
		    >
		      <v-img
		      	@click="goToProduct(product.id)"
		        :aspect-ratio="16/9"
		        
		        :src="website.api.url +'storage_base/'+ product.image"
		      >
		        <v-expand-transition>
		          <div
		          	@click="goToProduct(product.id)"
		            v-if="hover"
		            class="d-flex transition-fast-in-fast-out deep-orange v-card--reveal display-3 white--text"
		            style="height: 100%;"
		          >
		            ${{ product.price }}
		          </div>
		        </v-expand-transition>
		      </v-img>
		      <v-card-text
		      	@click="goToProduct(product.id)"
		        class="pt-4"
		        style="position: relative;"
		      >
		        <div class="font-weight-light grey--text title mb-2">{{product.category}}</div>
		        <h3 class="display-1 font-weight-light orange--text mb-2">{{ product.name }}</h3>
		        <div class="font-weight-light title mb-2">
		          {{ product.description }}
		        </div>
		      </v-card-text>
		    </v-card>
		  </v-hover>
        <v-list class="hidden-md-and-up" subheader>
          <v-subheader>Productos</v-subheader>
		   <v-list-tile class="hidden-md-and-up">
	          <v-text-field
			    name="name"
			    label="Buscar Productos"
			    flat
		        solo-inverted
		        hide-details
		        light
		        v-model="searcher"
		        prepend-inner-icon="search"
		       	v-on:keyup="verifyIfIsEmpty()"
		        @keyup.enter="findProducts()"
			  ></v-text-field>
			</v-list-tile>
          <v-list-tile
            v-for="(product,index) in productos"
            :key="index"
            avatar
            @click=""
          >
            <v-list-tile-avatar>
                <img :src="website.api.url + 'storage_base/'+product.image">
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>{{ product.name }}</v-list-tile-title>
              <v-list-tile-sub-title>{{ product.category }}</v-list-tile-sub-title>
            </v-list-tile-content>

            <v-list-tile-action>
              <v-tooltip left>
                <v-btn icon ripple slot="activator" @click="goToProduct(product.id)">
                	<v-icon color="grey darken-1">chevron_right</v-icon>
              	</v-btn>
              	<span>Más información sobre este producto.</span>
              </v-tooltip>
            </v-list-tile-action>
          </v-list-tile>
          <v-subheader>
          	 <div>Icons made by <a href="https://www.flaticon.com/authors/pixel-buddha" title="Pixel Buddha">Pixel Buddha</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div>
          </v-subheader>
        </v-list>
        <v-dialog v-model="showProduct" max-width="500px">
        <v-card>
          <v-card-title>
            Producto {{ product.title }}
          </v-card-title>
          <v-card-text>
            <v-btn color="primary" dark @click="showProduct = !showProduct">Open Dialog 3</v-btn>
            
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" flat @click="showProduct=false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
       <v-tooltip left>
	       <v-btn
				slot="activator"
				color="deep-orange"
				dark
				medium
				fixed
				bottom
				right
				fab
				@click="openCategorySelector()"
			>
	          <v-icon>label</v-icon>
	        </v-btn>
	        <span>Seleccione la categoria en la que desea filtrar</span>
	    </v-tooltip>
	    <div class="selectCategory animated fadeIn" v-show="categorySelector.active">
	    	<v-btn flat icon class="cross" @click="closeCategorySelector()">
	    		<v-icon>clear</v-icon>
	    	</v-btn>
	    	<div class="categorias">
	    		<center>
	    			<v-tooltip top>
	    				<div class="categoria" slot="activator" @click="setCategory('tecnologia')">
	    					<img :src="categorySelector.tecnologyImage">
	    				</div>
	    				<span>Categoría de tecnología</span>
	    			</v-tooltip>
	    			<v-tooltip top>
			    		<div class="categoria" slot="activator" @click="setCategory('limpieza')"> 
			    			<img :src="categorySelector.cleaningImage">
			    		</div>
			    		<span>Categoría de Limpieza (Hogar y Ropa)</span>
					</v-tooltip>
		    		<v-tooltip top>
		    			<div class="categoria" slot="activator" @click="setCategory('enlatados')">
		    				<img :src="categorySelector.cannedFoodImage">
		    			</div>
		    			<span>Categoría de enlatados</span>
		    		</v-tooltip>
		    		<v-tooltip top>
	    				<div class="categoria" slot="activator" @click="setCategory('')">
	    					<img :src="categorySelector.nothingImage" class="crossImage">
	    				</div>
	    				<span>Eliminar categoria seleccionada</span>
	    			</v-tooltip>
		    	</center>
	    	</div>
	    </div>
		<br>
		<div class="hidden-sm-and-down" :style="{'margin-left':'3%'}">Icons made by <a href="https://www.flaticon.com/authors/pixel-buddha" title="Pixel Buddha">Pixel Buddha</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div>
	</div>
</template>
<script>
	import headphonesImage from '../../assets/headphones.svg';
	import sprayImage from '../../assets/spray.svg';
	import canImage from '../../assets/canned-food.svg';
	import crossImage from '../../assets/cross.svg';
	export default{
		mounted()
		{
			console.log(this.$store.getters.getProducts);
			// this.$store.commit('SearcherActiveState',true);
			window.scrollTo(0,0);
			document.querySelector('html').style['overflow-y']='visible';
			if(this.$route.query.c != null)
			{
				if(this.categorySelector.possiblesCategories.includes(this.$route.query.c.toLowerCase()))
				{
					this.categorySelector.category = this.$route.query.c.toLowerCase();
				}else{
					this.categorySelector.category = null;
				}
			}
			this.wantSeeChanged();
		},
		data:()=>({
			searcher:'',
	        showProduct:false,
	        withoutProductsAlert:false,
	        wantSee:10,
	        wantSeePorcent:100,
	        product:{ // (SELECTED PRODUCT)

	        },
	        categorySelector:{
	        	active: false,
	        	category:null,
	        	tecnologyImage:headphonesImage,
	        	cleaningImage:sprayImage,
	        	cannedFoodImage: canImage,
	        	nothingImage: crossImage,
	        	possiblesCategories:['tecnologia','limpieza','enlatados']
	        }
		}),

		methods:{
			setCategory(category)
			{
				this.categorySelector.active=false;
				document.querySelector('html').style['overflow-y']='visible';
				if(category == '')
				{
					this.$router.push({path:'/productos'});

				}
				if(this.categorySelector.possiblesCategories.includes(category.toLowerCase()))
				{
					this.$router.push({path:'/productos',query:{c:category.toLowerCase()}});
				}else{
					this.categorySelector.category = null;
				}
			},
			wantSeeChanged()
			{
				if(this.productos != null)
				{
					var cantidad_productos = this.productos.length;
					var wantSee = this.wantSeePorcent * cantidad_productos;
					wantSee = wantSee / 100;
					this.wantSee = wantSee;
				}				
			},
			openCategorySelector()
			{
				document.querySelector('html').style['overflow-y']='hidden';
				this.categorySelector.active=true;
			},
			closeCategorySelector()
			{
				document.querySelector('html').style['overflow-y']='visible';
				this.categorySelector.active=false;
			},
			goToProduct(product_id)
			{
				this.$router.push({path:'/producto', query:{product_code:product_id}});
			},
			findProducts()
			{
				this.$store.commit('changeSearcherState',this.searcher);
			},
			verifyIfIsEmpty()
			{
				if(this.searcher.length == 0)
				{
					this.findProducts();
				}
			},

		},
		computed:{
			website()
			{
				return this.$store.getters.getWebsiteInfo;
			},
			productos()
			{
				console.log("hello world")
				if(this.$store.getters.getProducts != null)
				{
					this.withoutProductsAlert = false;
					if(this.categorySelector.category!=null)
					{
						return this.$store.getters.getProducts.filter((product) => product.category.toLowerCase() == this.categorySelector.category.toLowerCase());
					}else{
						return this.$store.getters.getProducts;
					}
				}else{
					this.withoutProductsAlert = true;
				}
			}
		},
		watch:{
			'$route'(to,from)
			{
				if(to.query.c != null)
				{
					if(this.categorySelector.possiblesCategories.includes(to.query.c.toLowerCase()))
					{
						this.categorySelector.category = to.query.c.toLowerCase();
					}else{
						this.categorySelector.category = null;
					}
				}
			}
		}
	}
</script>
<style lang="scss">
	.v-card--reveal {
	  align-items: center;
	  bottom: 0;
	  justify-content: center;
	  opacity: .5;
	  position: absolute;
	  width: 100%;
	}
	.selectCategory{
		width: 100% !important;
		height: 100% !important;
		position: fixed !important;
		z-index: 20000 !important;
		background-color: #fff !important;
		top: 0 !important;
		overflow-y: scroll !important;
		.cross
		{
			right: 10px;
			position: fixed !important;
			top: 10px !important;
			z-index: 200000 !important;
		}
		.categorias{
			height: 50vh !important;
			width: 100% !important;
			margin-top: 25vh !important;
			.categoria
			{
				margin-top: 20px !important;
				display: inline-block !important;
				margin-left: 10px !important;
				cursor: pointer !important;
				transition: all 0.3s ease !important;
				border-radius: 50% !important;
				background-color: #FAFAFA !important;
				width: 200px !important;
				height: 200px !important;
				img{
					margin-top: 30px;
					width: 125px !important;
					height: 125px !important;
				}
				img.crossImage
				{
					margin-top: 37.5px !important;
				}
				&:hover
				{
					transition: all 0.3s ease !important;
					background-color: #FFEBEE !important;
				}
			}
		}
	}
</style>


