<template>
	<div>
		<v-toolbar color="primary"  dark app>
		  <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
			<v-btn @click="goTo('/productos')" icon large flat><v-icon large>chevron_left</v-icon></v-btn>
	        <span>{{ website.title }} </span>
	      </v-toolbar-title>
		</v-toolbar>
		<v-content :style="{'padding-top':'120px'}" v-if="producto.name">
			<v-layout row wrap >
		  		<v-flex xs12 md12 v-if="producto.name != null">
		  			<center class="hidden-md-and-up">
		  				<center>
		  					<v-card :style="{'width:':'100%','min-height':'100vh', 'padding-bottom':'50px'}">
						        <v-img
						          :src="website.api.url + 'storage_base/'+producto.image"
						        ></v-img>

						        <v-card-title primary-title :style="{'width':'100%'}">
						          <div :style="{'width':'100%'}">

						          	<strong v-if="producto.marca != null" :style="{'color':'grey','padding':'0px'}">
						          		Marca: {{ producto.marca }}
						          	</strong>

						            <h3 class="headline mb-0">{{ producto.name }}</h3>
						            <div>{{ producto.description }}
						            <br>
									<v-btn block :style="{'width':'100%'}" color="blue darken-1" dark>${{ producto.price }}</v-btn>
						        </div>
					          </div>
					        </v-card-title>
					      </v-card>
					  </center>
		  			</center>
		  			<div class="desktop hidden-sm-and-down">
		  			<v-card :style="{'padding':'70px'}">
				       <center>
				       		<v-img
					        	v-if="producto.image != null"
					          	:src="website.api.url + 'storage_base/'+producto.image"
					          	aspect-ratio="2.75"
					          	:style="{'width':'200px','height':'200px'}"
				        	></v-img>
				        </center>

				        <v-card-title primary-title :style="{'width':'100%'}">
				          <div :style="{'width':'100%','text-align':'center'}">
				          	<strong>
				          		Categoria: {{ producto.category }}
				          	</strong>
				          	<br>
				          	<strong v-if="producto.marca != null">
				          		Marca: {{ producto.marca }}
				          	</strong>
				            <h3 class="headline mb-0">		  				{{ producto.name }}</h3>
				            <div>{{ producto.description }}</div>
				            <v-btn color="blue" dark>
				            	<v-icon>attach_money</v-icon>
				            	{{ producto.price }}
				            </v-btn>
				          </div>
				        </v-card-title>
				      </v-card>

		  			</div>
		  		</v-flex>
			</v-layout>
		</v-content>
		<div :style="{'width':'100vw','height':'100vh'}"  v-else>
			<v-container fill-height>
		      <v-layout align-center>
		        <v-flex text-xs-center>
					<v-progress-circular indeterminate color="purple" :size="50" :width="5"></v-progress-circular>
		        </v-flex>
		      </v-layout>
		    </v-container>
		</div>
	</div>
</template>
<script>
	import axios from 'axios';
	export default
	{
		mounted()
		{
			this.getProduct();
		},
		data:()=>({
			producto:{
				name:null,
				image:null,
				category:'',
				price:'',
			}
		}),
		methods:{
			goTo(param)
			{
				this.$router.push(param);
			},
			getProduct()
			{
				if(this.$route.query.product_code == null)
				{
					this.$router.push({path:'/productos'});
				}else{
					var obj = this;
					var product_id = this.$route.query.product_code;
					axios.get(`${this.website.api.url}productos/${product_id}`).then((response)=>{
						obj.producto = response.data;
						console.log(response.data)
					})
				}
			}
		},
		computed:{
			website()
			{
				return this.$store.getters.getWebsiteInfo;
			}
		}
	}
</script>
<style lang="scss">
	
</style>


