import Vue from 'vue'
import Router from 'vue-router'
//GUEST PAGES
import GuestHome from '@/pages/guest/Home';
import GuestProductos from '@/pages/guest/Productos';
import GuestProducto from '@/pages/guest/Producto';

//Layouts
import GuestLayout from '@/pages/layouts/GuestLayout';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      component: GuestLayout,
      children:[
	      {
	      	path:'',
	      	component:GuestHome,
	      	name: 'GuestHome'
	      },
	      {
	      	path:'productos',
	      	component:GuestProductos,
	      	name:'GuestProductos'
	      }
      ]
    },
    {
    	path:'/producto',
    	component:GuestProducto,
    	name:'GuestProducto'
    }
  ]
})



