<template>
	<div>
		<v-navigation-drawer
		  class="hidden-md-and-up"
	      v-model="drawer"
	      :style="{'z-index':'1002'}"
	      fixed
	      app
	    >
	      <v-list dense>
	        <template v-for="(item,index) in items">
	          <v-layout
	            v-if="item.heading"
	            :key="index"
	            row
	            align-center
	          >
	            <v-flex xs6>
	              <v-subheader v-if="item.heading">
	                {{ item.heading }}
	              </v-subheader>
	            </v-flex>
	            <v-flex xs6 class="text-xs-center">
	              <a href="#!" class="body-2 black--text">EDIT</a>
	            </v-flex>
	          </v-layout>
	          <v-list-group
	            v-else-if="item.children"
	            v-model="item.model"
	            :key="index"
	            :prepend-icon="item.model ? item.icon : item['icon-alt']"
	            append-icon=""
	          >
	            <v-list-tile slot="activator">
	              <v-list-tile-content>
	                <v-list-tile-title>
	                  {{ item.text }}
	                </v-list-tile-title>
	              </v-list-tile-content>
	            </v-list-tile>
	            <v-list-tile
	              v-for="(child, i) in item.children"
	              :key="i"
	              @click=""
	            >
	              <v-list-tile-action v-if="child.icon">
	                <v-icon>{{ child.icon }}</v-icon>
	              </v-list-tile-action>
	              <v-list-tile-content>
	                <v-list-tile-title>
	                  {{ child.text }}
	                </v-list-tile-title>
	              </v-list-tile-content>
	            </v-list-tile>
	          </v-list-group>
	          <v-list-tile v-else :key="item.text" @click="go(item.url)">
	            <v-list-tile-action>
	              <v-icon>{{ item.icon }}</v-icon>
	            </v-list-tile-action>
	            <v-list-tile-content>
	              <v-list-tile-title>
	                {{ item.text }}
	              </v-list-tile-title>
	            </v-list-tile-content>
	          </v-list-tile>
	        </template>
	      </v-list>
	    </v-navigation-drawer>
	    <v-toolbar
	      :clipped-left="$vuetify.breakpoint.lgAndUp"
	      color="primary"
	      dark
	      app
	      fixed
	      :style="{'z-index':'1000'}"
	    >
	      <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
	        <v-toolbar-side-icon class="hidden-md-and-up" @click.stop="drawer = !drawer"></v-toolbar-side-icon>
	        <span>{{ website.title }} </span>
	      </v-toolbar-title>
	      <v-text-field
		        flat
		        solo-inverted
		        hide-details
		        v-model="searcher"
		        prepend-inner-icon="search"
		        label="Para buscar presiona ENTER."
		        v-on:keyup="verifyIfIsEmpty()"
		        v-show="website.searcherActive"
				@keyup.enter="findProducts()"
		        class="hidden-sm-and-down"
	      ></v-text-field>
	      <v-spacer ></v-spacer>
	      <v-toolbar-items class="hidden-sm-and-down">
	      		<v-btn flat v-for="(item,index) in items" :key="index" @click="go(item.url)">
		        	{{ item.text }}
		      	</v-btn>
		  </v-toolbar-items>
	    </v-toolbar>
	</div>
</template>
<script>
	export default
	{
		props:['items'],
		data:()=>({
			searcher:'',
			drawer: false,
		}),
		methods:{
			go(param)
			{
				this.$router.push(param);
			},
			findProducts()
			{
				this.$store.commit('changeSearcherState',this.searcher);
			},
			verifyIfIsEmpty()
			{
				if(this.searcher == '')
				{
					this.findProducts();
				}
			}
		},
		computed:{
			website()
			{
				return this.$store.getters.getWebsiteInfo;
			}
		}
	}
</script>


